rgin: 4px;\n$row-height: 27px;\n\n$button-height: 20px;\n\n.dg {\n\n  /** Clear list styles */\n  ul {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n    width: 100%;\n    clear: both;\n  }\n\n  /* Auto-place container */\n  &.ac {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    height: 0;\n    z-index: 0;\n  }\n\n  &:not(.ac) .main {\n    /** Exclude mains in ac so that we don't hide close button */\n    overflow: hidden;\n  }\n\n  &.main {\n\n    @include transition(opacity, 0.1s, linear);\n\n    &.taller-than-window {\n\n      overflow-y: auto;\n\n      .close-button {\n\n        opacity: 1;\n\n        /* TODO, these are style notes */\n        margin-top: -1px;\n        border-top: 1px solid $border-color;\n\n      }\n\n    }\n\n    ul.closed .close-button {\n      opacity: 1 !important;\n    }\n\n    &:hover .close-button,\n    .close-button.drag {\n      opacity: 1;\n    }\n\n    .close-button {\n      /*opacity: 0;*/\n      @include transition(opacity, 0.1s, linear);\n      border: 0;\n\n      line-height: $button-height - 1;\n      height: $button-height;\n\n      /* TODO, these are style notes */\n      cursor: pointer;\n      text-align: center;\n      background-color: #000;\n      &.close-top {\n        position: relative;\n      }\n      &.close-bottom {\n        position: absolute;\n      }\n      &:hover {\n        background-color: #111;\n      }\n\n    }\n\n  }\n\n  /* Auto-placed GUI's */\n  &.a {\n\n    float: right;\n    margin-right: 15px;\n    overflow-y:visible;\n\n    &.has-save > ul {\n\n      &.close-top {\n        margin-top: 0;\n      }\n      &.close-bottom {\n        margin-top: $row-height;\n      }\n\n      &.closed {\n        margin-top: 0;\n      }\n    }\n\n    .save-row {\n      top: 0;\n      z-index: 1002;\n\n      &.close-top {\n        position: relative;\n      }\n      &.close-bottom {\n        position: fixed;\n      }\n\n    }\n\n  }\n\n  li {\n    @include transition(height, 0.1s, ease-out);\n    @include transition(overflow, 0.1s, linear);\n  }\n\n  /* Line items that don't contain folders. */\n  li:not(.folder) {\n    cursor: auto;\n    height: $row-height;\n    line-height: $row-height;\n    padding: 0 4px 0 5px;\n  }\n\n  li.folder {\n    padding: 0;\n    border-left: $nest-margin solid rgba(0, 0, 0, 0);\n\n  }\n\n  /** Folder names */\n  li.title {\n    cursor: pointer;\n    margin-left: -$nest-margin;\n  }\n\n  /** Hides closed items */\n  .closed li:not(.title),\n  .closed ul li,\n  .closed ul li > * {\n    height: 0;\n    overflow: hidden;\n    border: 0;\n  }\n\n  /** Controller row */\n  .cr {\n    clear: both;\n    padding-left: 3px;\n    height: $row-height;\n    overflow: hidden;\n  }\n\n  /** Name-half (left) */\n  .property-name {\n    cursor: default;\n    float: left;\n    clear: left;\n    width: 40%;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n\n  /** Controller-half (right) */\n  .c {\n    float: left;\n    width: 60%;\n    position: relative;\n  }\n\n  /** Controller placement */\n  .c input[type=text] {\n    border: 0;\n    margin-top: 4px;\n    padding: 3px;\n    width: 100%;\n    float: right;\n  }\n\n  /** Shorter number boxes when slider is present. */\n  .has-slider input[type=text] {\n    width: 30%;\n    /*display: none;*/\n    margin-left: 0;\n  }\n\n  .slider {\n    float: left;\n    width: 66%;\n    margin-left: -5px;\n    margin-right: 0;\n    height: 19px;\n    margin-top: 4px;\n  }\n\n  .slider-fg {\n    height: 100%;\n  }\n\n  .c input[type=checkbox] {\n    margin-top: 7px;\n  }\n\n  .c select {\n    margin-top: 5px;\n  }\n\n  /** Ensure the entire boolean and function row shows a hand */\n  .cr.function,\n  .cr.function .property-name, /* Don't know why I need to be this explicit */\n  .cr.function *,\n  .cr.boolean,\n  .cr.boolean * {\n    cursor: pointer;\n  }\n\n  /** allow overflow for color selector */\n  .cr.color {\n    overflow: visible;\n  }\n\n  .selector {\n    display: none;\n    position: absolute;\n    margin-left: -9px;\n    margin-top: 23px;\n    z-index: 10;\n  }\n\n  .c:hover .selector,\n  .selector.drag {\n    display: block;\n  }\n\n  li.save-row {\n\n    padding: 0;\n\n    .button {\n      display: inline-block;\n      padding: 0px 6px;\n    }\n\n  }\n\n  &.dialogue {\n    background-color: #222;\n    width: 460px;\n    padding: 15px;\n    font-size: 13px;\n    line-height: 15px;\n  }\n\n}\n\n/* TODO Separate style and structure */\n#dg-new-constructor {\n  padding: 10px;\n  color: #222;\n  font-family: Monaco, monospace;\n  font-size: 10px;\n  border: 0;\n  resize: none;\n  box-shadow: inset 1px 1px 1px #888;\n  word-wrap: break-word;\n  margin: 12px 0;\n  display: block;\n  width: 440px;\n  overflow-y: scroll;\n  height: 100px;\n  position: relative;\n}\n\n#dg-local-explain {\n  display: none;\n  font-size: 11px;\n  line-height: 17px;\n  border-radius: 3px;\n  background-color: #333;\n  padding: 8px;\n  margin-top: 10px;\n  code {\n    font-size: 10px;\n  }\n}\n\n#dat-gui-save-locally {\n  display: none;\n}\n","$background-color: #1a1a1a;\n\n$hover-lighten: 5%;\n$border-lighten: 7%;\n$active-lighten: 10%;\n\n$number-color: #2FA1D6;\n$boolean-color: #806787;\n$string-color: #1ed36f;\n$function-color: #e61d5f;\n$save-row-color: #dad5cb;\n$button-color: darken($save-row-color, 10%);\n$border-color: lighten($background-color, $border-lighten);\n$input-color: lighten($background-color, 8.5%);\n\n@mixin transition($prop, $time, $curve) {\n  -webkit-transition: $prop $time $curve;\n  -o-transition: $prop $time $curve;\n  -moz-transition: $prop $time $curve;\n  transition: $prop $time $curve;\n}\n\n@mixin gradient($a, $b) {\n  background: -webkit-gradient(linear, 0% 0%, 0% 100%, from($a), to($b));\n  background: -o-gradient(linear, 0% 0%, 0% 100%, from($a), to($b));\n  background: -moz-gradient(linear, 0% 0%, 0% 100%, from($a), to($b));\n}\n\n@mixin button() {\n  margin-left: 5px;\n  margin-top: 1px;\n  border-radius: 2px;\n  font-size: 9px;\n  line-height: 7px;\n  padding: 4px 4px 5px 4px;\n  background: $button-color;\n  color: #fff;\n  text-shadow: 0 1px 0 darken($button-color, 10%);\n  box-shadow: 0 -1px 0 darken($button-color, 10%);\n  cursor: pointer;\n}\n\n@mixin gears() {\n  background: $button-color url(data:image/png;base64,) 2px 1px no-repeat;\n  height: 7px;\n  width: 8px;\n}\n\n@import \"structure\";\n\n/** Main type */\n.dg {\n\n  color: #eee;\n  font: 11px 'Lucida Grande', sans-serif;\n  text-shadow: 0 -1px 0 #111;\n\n  /** Auto place */\n  &.main {\n\n    /** Scrollbar */\n    &::-webkit-scrollbar {\n      width: 5px;\n      background: $background-color;\n    }\n    &::-webkit-scrollbar-corner {\n      height: 0;\n      display: none;\n    }\n    &::-webkit-scrollbar-thumb {\n      border-radius: 5px;\n      background: lighten($background-color, 30%);\n    }\n\n  }\n\n  li {\n\n    &:not(.folder) {\n      background: $background-color;\n      border-bottom: 1px solid $border-color;\n    }\n\n    &.save-row {\n\n      line-height: 25px;\n      background: $save-row-color;\n      border: 0;\n\n      select {\n        margin-left: 5px;\n        width: 108px;\n\n      }\n\n      .button {\n\n        &.gears {\n          @include gears;\n        }\n\n        @include button;\n\n        &:hover {\n          background-color: darken($button-color, 5%);\n          box-shadow: 0 -1px 0 darken($button-color, 10%);\n        }\n\n      }\n\n    }\n\n    &.folder {\n      border-bottom: 0;\n    }\n\n    &.title {\n      padding-left: 16px;\n      background: #000 url(data:image/gif;base64,R0lGODlhBQAFAJEAAP////Pz8////////yH5BAEAAAIALAAAAAAFAAUAAAIIlI+hKgFxoCgAOw==) 6px 10px no-repeat;\n      cursor: pointer;\n      border-bottom: 1px solid rgba(255, 255, 255, 0.2);\n    }\n\n  }\n\n  .closed li.title {\n    background-image: url(data:image/gif;base64,R0lGODlhBQAFAJEAAP////Pz8////////yH5BAEAAAIALAAAAAAFAAUAAAIIlGIWqMCbWAEAOw==);\n  }\n\n  /* Controller row, <li> */\n  .cr {\n\n    &.boolean {\n      border-left: 3px solid $boolean-color;\n    }\n\n    &.color {\n      border-left: 3px solid;\n    }\n\n    &.function {\n      border-left: 3px solid $function-color;\n    }\n\n    &.number {\n      border-left: 3px solid $number-color;\n      input[type=text] {\n        color: $number-color;\n      }\n    }\n\n    &.string {\n      border-left: 3px solid $string-color;\n      input[type=text] {\n        color: $string-color;\n      }\n    }\n\n    &.function:hover,\n    &.boolean:hover {\n      background: #111;\n    }\n\n  }\n\n  /** Controllers */\n  .c {\n\n    input[type=text] {\n\n      background: $input-color;\n      outline: none;\n      &:hover {\n        background: lighten($input-color, $hover-lighten);\n      }\n      &:focus {\n        background: lighten($input-color, $active-lighten);\n        color: #fff;\n      }\n\n    }\n\n    .slider {\n      background: $input-color;\n      cursor: ew-resize;\n    }\n\n    .slider-fg {\n      background: $number-color;\n      max-width: 100%;\n    }\n\n    .slider:hover {\n      background: lighten($input-color, $hover-lighten);\n      .slider-fg {\n        background: lighten($number-color, $hover-lighten);\n      }\n    }\n\n  }\n\n}\n"],"sourceRoot":""}]);
